body{
  overflow: hidden;
  font-family: Arial, Helvetica, sans-serif;
  // width: 100%;
    max-width: 100%;
    min-height: 100%;
// background-image: url('../src/image/1.jpg');
background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed; 
  transition: background-image 1s ease-in-out;
}
header{
}
main{
  button{
    background-color: rgba(255, 0, 0, 0);
    border: none;
    width: 32px;
    height: 32px;
  }
  .centerMain{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 100vh;
    
  }
  .time{
    font-weight: bold;
    color: white;
    font-size: 100px;
    letter-spacing: 4px;
  }
  .date{
    color: white;
    font-size: 30px;
    letter-spacing: 4px;
  }
  .slider-icon{
    position: absolute;
    top: 50%;
    &:active{
    transform: scale(1.1);
    }
  } 
  
  .slide-prev{
    left: 20px;
  background-image: url(../src/icons/slider-prev.svg);
  background-size:cover;
  }
  .slide-next{
    right: 20px;
    background-image: url(../src/icons/slider-next.svg);
    background-size:cover;
  }
}
.greeting{
  color: white;
  padding: 10px;
  font-size: 40px;
  flex: 1;
  text-align: right;
}
.user-name{
  text-align: left;
  color: white;
  font-size: large;
  background-color: rgba(240, 248, 255, 0);
  border: none;
  font-size: 40px;
  flex: 1;
  &:focus-visible{
    outline: none;
  }
}
.user-name::placeholder {
  color: rgb(221, 212, 212);
}
.col-6{
  display: flex;
  width: 50%;
  flex-wrap:wrap;
  justify-content: stretch;
  align-items: center;
}
.col-12{
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
}
.row{
display: flex;
}